<template>
  <div class="linear" ref='linear' style='width: 400px;height: 300px;'>

  </div>
</template>

<script>
  export default{
    mounted(){
      this.init();
    },
    methods: {
      init(){
        let myChart = this.$echarts.init(this.$refs.linear);
        var  category= [{
                 name: "220.182.8.7",
                value: 10
            },
        ]; // 类别
        let colorList = [{
          startcolor: '#9777FF',
          endcolor: '#5659F5'
        },{
          startcolor: '#9777FF',
          endcolor: '#5659F5'
        },{
          startcolor: '#9777FF',
          endcolor: '#5659F5'
        },{
          startcolor: '#9777FF',
          endcolor: '#5659F5'
        },{
          startcolor: '#9777FF',
          endcolor: '#5659F5'
        },]
        var  datas = [];
        category.forEach(value => {
            datas.push(value.value);
        });
        let option = {
            backgroundColor:'transparent ',
            xAxis: {
                splitLine: {
                    show: false
                },
                axisLine: {
                    show: false
                },
                axisLabel: {
                    show: false
                },
                axisTick: {
                    show: false
                }
            },
            grid: {
                left: 120,
                top: 20, // 设置条形图的边距
                right: 80,
                bottom: 20
            },
            yAxis: [{
                type: "category",
                inverse: true,
                data: category,
                axisLine: {
                    show: false
                },
                axisTick: {
                    show: false
                },
                axisLabel: {
                    show: false
                }
            }],
            series: [{
                    // 内
                    type: "bar",
                    barWidth: 18,

                    legendHoverLink: false,
                    silent: true,
                    itemStyle: {
                        normal: {
                            barBorderRadius:[0,0,0,0],
                            color: function(params) {
                                var color;
                                    color = {
                                        type: "linear",
                                        x: 0,
                                        y: 0,
                                        x2: 1,
                                        y2: 0,
                                        colorStops: [{
                                                 offset: 0,
                                                color: colorList[params.dataIndex].startcolor // 0% 处的颜色
                                            },
                                            {
                                                offset: 1,
                                                color: colorList[params.dataIndex].endcolor // 100% 处的颜色
                                            }
                                        ]
                                    }

                                    return color;
                            },
                        }
                    },
                    label: {
                        normal: {
                            show: true,
                            position: "left",
                            formatter: "{b}",
                            textStyle: {
                                color: "#fff",
                                fontSize: 13
                            }
                        }
                    },
                    data: category,
                    z: 1,
                    animationEasing: "elasticOut"
                },
                {
                    // 分隔
                    type: "pictorialBar",
                    itemStyle: {
                        normal:{
                            color:"#000"
                        }
                    },
                    symbolRepeat: "fixed",
                    symbolMargin: 2,
                    symbol: "rect",
                    symbolClip: true,
                    symbolSize: [5, 21],
                    symbolPosition: "start",
                    symbolOffset: [0, -1],
                    data: category,
                    z: 2,
                    animationEasing: "elasticOut"
                },
                {
                    // 外边框
                    type: "pictorialBar",
                    symbol: "rect",
                    itemStyle: {
                        normal: {
                            color: "none"
                        }
                    },
                    label: {
                        normal: {
                        	formatter: (params) => {
                        		var text;
                                if(params.dataIndex==1){
                                	text = '{f|  '+params.data+'}';
                                }else if(params.dataIndex==2){
                                	text = '{f|  '+params.data+'}';
                                }else if(params.dataIndex==3){
                                	text = '{f|  '+params.data+'}';
                                }else{
                                	text = '{f|  '+params.data+'}';
                                }
                                return text;
            				},
            				rich:{
            					f:{
            			        	color:"#ffffff"
            			        }
            				},
            				position: 'right',
            				show: true
                        }
                    },
                    data: datas,
                    z: 0,
                    animationEasing: "elasticOut"
                }
            ]
        };

        myChart.setOption(option);

      }
    }
  }
</script>

<style>
</style>
