<template>
  <div class="linear1">
    <div class="linear" ref='linear' style='width: 520px; height: 400px;'>

    </div>
  </div>
</template>

<script>
  export default{
    data(){
      return{

      }
    },
    mounted(){
        this.linearinit();
    },
    methods: {
      linearinit(){
        let myChart = this.$echarts.init(this.$refs.linear);
        let bgColor = "transparent";
        let color = [
            "#0090FF",
            "#5E81C0",
            "#FFC005",
            "#FF515A",
            "#8B5CFF",
            "#00CA69"
        ];
        let echartData = [{
                name: "周一",
                value2: 1500
            },
            {
                name: "周二",
                value2: 4000
            },
            {
                name: "周三",
                value2: 1000
            },
            {
                name: "周四",
                value2: 2500
            },
            {
                name: "周五",
                value2: 3000
            },
            {
                name: "周六",
                value2: 2500
            },
            {
                name: "周日",
                value2: 2100
            }
        ];

        let xAxisData = echartData.map(v => v.name);
        //  ["1", "2", "3", "4", "5", "6", "7", "8"]
        let yAxisData1 = echartData.map(v => v.value1);
        // [100, 138, 350, 173, 180, 150, 180, 230]
        let yAxisData2 = echartData.map(v => v.value2);
        // [233, 233, 200, 180, 199, 233, 210, 180]
        const hexToRgba = (hex, opacity) => {
            let rgbaColor = "";
            let reg = /^#[\da-f]{6}$/i;
            if (reg.test(hex)) {
                rgbaColor = `rgba(${parseInt("0x" + hex.slice(1, 3))},${parseInt(
              "0x" + hex.slice(3, 5)
            )},${parseInt("0x" + hex.slice(5, 7))},${opacity})`;
            }
            return rgbaColor;
        }

        let option = {
            backgroundColor: bgColor,
            color: color,
            grid: {
                top: 100,
                containLabel: true
            },
            xAxis: [{
                type: "category",
                boundaryGap: false,
                axisLabel: {
                    formatter: '{value}',
                    textStyle: {
                        color: "#fff"
                    }
                },
                axisLine: {
                    lineStyle: {
                        color: "#D9D9D9"
                    }
                },
                data: xAxisData
            }],
            yAxis: [{
                type: "value",
                axisLabel: {
                    textStyle: {
                        color: "#fff"
                    }
                },
                nameTextStyle: {
                    color: "#666",
                    fontSize: 12,
                    lineHeight: 40
                },
                splitLine: {
                    lineStyle: {
                        type: "dashed",
                        color: "#E9E9E9"
                    }
                },
                axisLine: {
                    show: false
                },
                axisTick: {
                    show: false
                }
            }],
            series: [ {
                name: "2019",
                type: "line",
                smooth: true,
                // showSymbol: false,
                symbolSize: 0,
                zlevel: 3,
                lineStyle: {
                    normal: {
                        color: color[1],
                        shadowBlur: 3,
                        shadowColor: hexToRgba(color[1], 0.5),
                        shadowOffsetY: 0
                    }
                },
                areaStyle: {
                    normal: {
                        color: new this.$echarts.graphic.LinearGradient(
                            0,
                            0,
                            0,
                            1,
                            [{
                                    offset: 0,
                                    color: hexToRgba(color[1], 0.3)
                                },
                                {
                                    offset: 1,
                                    color: hexToRgba(color[1], 0.1)
                                }
                            ],
                            false
                        ),
                        shadowColor: hexToRgba(color[1], 0.1),
                        shadowBlur: 10
                    }
                },
                data: yAxisData2
            }]
        };


        myChart.setOption(option);



      }
    }
  }
</script>

<style lang='less' scoped>
.linear1{
  margin-top: 30px;
}
</style>
